.switch {
    border: 0.2em solid black;
    border-radius: 15%;
    background-color: black;
    width: 2.6em;
    height: 1.25em;
    position: relative;
}

.switch-button {
    width: 50%;
    height: 100%;
    background-color: gray;
    border-radius: 10%;
    position: absolute;
}

.switch-button.switch-on {
    -webkit-transition: left 100ms ease;
    -o-transition: left 100ms ease;
    transition: left 100ms ease;
    left: 50%;
}

.switch-button.switch-off {
    -webkit-transition: left 100ms ease;
    -o-transition: left 100ms ease;
    transition: left 100ms ease;
    left: 0px;
}


